.form {
    position: absolute;
    top: 6vw;
    padding: 0 25vw;
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    align-items: center;


.name, {
    width: 100%;
    height: 3vw;
}

textarea {
    width: 100%;
}

label {
    text-transform: uppercase;
    font-weight: 400;
    font-size: calc(0.5vw + 10px);
    color: rgba(0,0,0,0.5);
    padding: 0.3vw 0 0 0.3vw;
    margin-bottom: 3vw;
}

.submit {
    text-transform: uppercase;
    width: 30%;
    height: 3vw;
    border-radius: 10vw;
    border: none;
    margin-top: 3vw;
}
.consent {
    width: 100%;
    display: grid;
    grid-template-columns: 5% 1fr;

.consentRadio {
        align-self: flex-start;
}

p {
    font-weight: 200;
    font-size: calc(1vw + 8px);
}
}
}