.container {
    width: 100vw;
    height: 100vh;

    .header {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        height: 25%;
        padding: 3vw;
        box-sizing: border-box;
        font-family: 'Barlow Condensed', sans-serif;
        font-weight: 600;
        text-transform: uppercase;
        text-shadow: 0 0 15px rgba(0, 0, 0, 0.3);

        .headline {
            top: 20%;
            width: 50vw;
            left: 0;
            position: absolute;
            z-index: -998;
        }


        .row {
            display: flex;
            z-index: -998;

            &.high {
                width: 50vw;
                height: 5.5vw;
            }

            img {

                height: 100%;
                max-height: 100%;
            }
        }

        h1 {
            font-size: calc(3.5vw + 24px);
            color: white;


            span {
                color: #244190;
                text-shadow:
                    -3px -3px 0 white,
                    3px -3px 0 white,
                    -3px 3px 0 white,
                    3px 3px 0 white;
            }
        }

        h2 {
            color: #ed1c2e;
            font-size: calc(2vw + 10px);

            span {
                color: #009d57;
            }
        }
    }

    .sky {

        top: 0;
        width: 130%;
        height: 100%;
        overflow-y: hidden;
        z-index: -999;

        img.gory {
            position: fixed;
            top: 10%;
            left: 50%;
            transform: translateX(-50%);
            width: auto;
            min-width: 100%;
            height: auto;
            z-index: 0;
            overflow: hidden;
        }

        img.chmury {
            position: absolute;
            top: 0%;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            min-width: 100%;
            z-index: -1000;
            min-height: 50%;
            width: auto;
            overflow: hidden;
        }
    }

    .footer {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 75%;
        width: 110%;

        img {
            position: absolute;
            top: 0;
        }

    }
}

.serek {
    position: absolute;

    &.almette {
        width: 22vw;
        left: 50%;
        transform: translate(-50%, -85%);
        z-index: 1
    }

    &.wiejski {
        width: 20vw;
        left: 35%;
        transform: translate(-50%, -80%);
        z-index: 1;
    }

    &.czteropak {
        width: 18vw;
        right: 18%;
        transform: translate(-50%, -82%);
        z-index: 1;
    }
}